footer{
	background: #131313;
	position: absolute;
	width: 100%;
	bottom: 0;

	.footer-container {
		display: flex;
		justify-content: space-between;
		flex-wrap: wrap;
		align-items:flex-end;
		position: relative;
		height: 56px;
		.footer-logo-block{
			align-items: flex-end;
			display: flex;
			justify-content: space-around;
			position: relative;
			.field--type-image{
				position: absolute;
				left: 0;
				.field__label{
					display:none;
				}
			}
			.field--name-body{
				padding-left: 8.5rem;
				.field__label{
					display:none;
				}
				p{
					color: #ccc;
				}
			}
		}
		.block-simple-social-icons{
			display: flex;
			justify-content: space-evenly;
			align-items: center;
			ul{
				li{
					margin: 0 .5rem;
					a{
						padding: 0 .2rem;
					}
				}
			}
			h2{
				font-size: 1rem;
				color: #ccc;
				text-transform: unset;
				margin: 0;
				line-height: 56px;
			}
		}
	}

}
